// Common theme settings
const common: Omit<ThemeOptions, 'palette' | 'shadows'> = { // Omit properties that differ per mode
  breakpoints: {
    values: {
      xs: 0,      // Mobile small
      sm: 480,    // Mobile large
      md: 768,    // Tablet
      lg: 1024,   // Laptop
      xl: 1280,   // Desktop
      // MUI v7 doesn't use 'xxl' by default, can add if needed
    }
  },
  direction: 'ltr' as const,
  shape: { borderRadius: 8 },
  typography: {
    fontFamily: '"DM Sans Variable", "Public Sans", sans-serif', // Ensure these fonts are loaded
    h1: { fontWeight: 700 },
    h2: { fontWeight: 700 },
    h3: { fontWeight: 700 },
    h4: { fontWeight: 600 },
    h5: { fontWeight: 600 },
    h6: { fontWeight: 600 },
    button: { fontWeight: 600 }
  },
  mixins: {
    // toolbar might be slightly different in v7, adjust if needed
    toolbar: { minHeight: 64 }
  }
};

// Create theme variants based on mode
const createCustomTheme = (mode: PaletteMode): ThemeOptions => {
  const isDark = mode === 'dark';

  // MUI v7 handles shadows differently, may need adjustment or removal
  // const shadowsArray = [ /* ... shadow definitions ... */ ];

  const palette = {
    mode,
    primary: PRIMARY,
    secondary: SECONDARY,
    info: INFO,
    success: SUCCESS,
    warning: WARNING,
    error: ERROR,
    grey: GREY, // Adjust grey scale usage if needed for v7
    common: { black: '#000000', white: '#FFFFFF' },
    
    ...(isDark 
      ? {
          // Dark mode color settings
          text: {
            primary: '#FFFFFF',
            secondary: '#A6B0BB', // GREY[600] equivalent for dark mode
            disabled: 'rgba(255, 255, 255, 0.5)'
          },
          background: {
            paper: '#1E1E2D',     // Slightly blue-tinted dark
            default: '#171723',   // Darker blue-tinted background
            // neutral might not be standard in v7 palette
            // neutral: '#131320', 
          },
          divider: 'rgba(145, 158, 171, 0.24)', // alpha(GREY[500], 0.24)
          action: {
            active: '#A6B0BB', // text.secondary
            hover: 'rgba(255, 255, 255, 0.08)',
            selected: 'rgba(255, 255, 255, 0.16)',
            disabled: 'rgba(255, 255, 255, 0.3)',
            disabledBackground: 'rgba(255, 255, 255, 0.12)'
          }
        } 
      : {
          // Light mode color settings
          text: {
            primary: GREY[800],
            secondary: GREY[600],
            disabled: GREY[500]
          },
          background: {
            paper: '#FFFFFF',
            default: GREY[100], 
            // neutral might not be standard in v7 palette
            // neutral: GREY[200], 
          },
          divider: alpha(GREY[500], 0.2),
          action: {
            active: GREY[600], // text.secondary
            hover: alpha(GREY[500], 0.08),
            selected: alpha(GREY[500], 0.16),
            disabled: alpha(GREY[500], 0.8),
            disabledBackground: alpha(GREY[500], 0.24)
          }
        }
    )
  };

  return {
    ...common, // Spread common settings
    palette,
    // shadows: shadowsArray, // Apply shadows if adapted for v7
    // Custom component overrides can be added here if needed
    components: {
       // Example: Customize Button default props
       MuiButton: {
           defaultProps: {
               disableElevation: true // Flat buttons by default
           },
           styleOverrides: {
               root: {
                   textTransform: 'none', // No ALL CAPS buttons
                   borderRadius: 8, // Match shape borderRadius
               },
               containedPrimary: {
                   '&:hover': {
                       // Add custom hover effect if needed
                       // boxShadow: isDark ? SHADOWS.dark.primary_hover : SHADOWS.light.primary_hover
                   }
               }
           }
       },
       MuiPaper: {
            defaultProps: {
                elevation: 0 // Use custom shadows or borders instead
            },
            styleOverrides: {
                root: {
                    backgroundImage: 'none', // Ensure no gradients unless intended
                },
                outlined: { // Style for outlined Paper
                    borderColor: isDark ? 'rgba(145, 158, 171, 0.24)' : alpha(GREY[500], 0.2),
                }
            }
       },
       MuiCard: {
            styleOverrides: {
                root: ({ theme }) => ({ // Use theme instance
                    borderRadius: 16,
                    // boxShadow: isDark ? SHADOWS.dark.card : SHADOWS.light.card, // Apply custom card shadow if adapted
                    position: 'relative',
                    zIndex: 0, // Fix stacking context issue with بعض MUI components
                })
            }
       },
       // Add more component customizations as needed
    }
  };
};

// Create light and dark themes
const lightThemeOptions = createCustomTheme('light');
const darkThemeOptions = createCustomTheme('dark');

// Export responsive themes
export const lightTheme = responsiveFontSizes(createTheme(lightThemeOptions));
export const darkTheme = responsiveFontSizes(createTheme(darkThemeOptions));

// Default export (e.g., light theme)
const theme = lightTheme; 
export default theme; 
